        const  Delay_time=60
        this.setState({DelayReasons,DisbursementDelayReasons,Delay_time})

        const DisbursementTimeRaw = {visits: [{x: "Under 1", y: 55}, {x: "1-2", y: 20}, {x: "2-4", y: 40},{x: "4-6", y: 60},{x: "6-8", y: 80},{x: "8-10", y: 30},{x: "10+", y: 50}],}
